.lmui-overlay {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 0.6;
  background: #000;
}
.lmui-overlay-enter {
  -webkit-animation: lmui-overlay-in 0.3s ease;
          animation: lmui-overlay-in 0.3s ease;
}
.lmui-overlay-leave {
  -webkit-animation: lmui-overlay-out 0.3s ease forwards;
          animation: lmui-overlay-out 0.3s ease forwards;
}
@-webkit-keyframes lmui-overlay-in {
  from {
    opacity: 0;
  }
}
@keyframes lmui-overlay-in {
  from {
    opacity: 0;
  }
}
@-webkit-keyframes lmui-overlay-out {
  to {
    opacity: 0;
  }
}
@keyframes lmui-overlay-out {
  to {
    opacity: 0;
  }
}
.lmui-actionsheet {
  background: #e0e0e0;
  width: 100%;
  text-align: center;
}
.lmui-actionsheet .lmui-actionsheet-container {
  list-style: none;
  padding: 0;
  margin: 0;
}
.lmui-actionsheet .lmui-actionsheet-container .lmui-actionsheet-item {
  display: block;
  width: 100%;
  height: 50px;
  line-height: 50px;
  font-size: 18px;
  color: #333;
  background-color: #fff;
  border-bottom: 1px solid #e0e0e0;
}
.lmui-actionsheet .lmui-actionsheet-container .lmui-actionsheet-item:active {
  background-color: #f0f0f0;
}
.lmui-actionsheet .lmui-actionsheet-container .lmui-actionsheet-cancel {
  margin-top: 5px;
}
